import 'flatpickr/dist/flatpickr.css'
import moment from 'moment'
import FlatPickr from 'vue-flatpickr-component'

export default {
  name: 'BaseDateRangeFilter',
  components: {
    FlatPickr
  },

  props: {
    value: {
      type: Object,
      default: null
    },
    showCheckBox: {
      type: Boolean,
      default: false
    },
    config: {
      type: Object,
      required: false,
      default: () => {
        return {}
      }
    },
    extendDate: {
      type: Boolean,
      default: true
    },
    label: {
      type: String,
      default: 'Date Range'
    }
  },
  data () {
    return {
      form: {
        start_date: null,
        end_date: null
      },
      checkBox: false
    }
  },

  computed: {
    endDateConfig () {
      /* eslint-disable vue/no-mutating-props */
      // eslint-disable-next-line vue/no-side-effects-in-computed-properties
      this.config.minDate = this.form.start_date
      return this.config
    }
  },

  watch: {
    value: {
      handler (newVal) {
        if (newVal.start_date !== null) {
          this.checkBox = true
          this.form.start_date = newVal.start_date
        }
        if (newVal.start_date == null && newVal.end_date == null) {
          this.checkBox = false
        }
        this.form.start_date = newVal.start_date
        this.form.end_date = newVal.end_date
      },
      immediate: true,
      deep: true
    },
    form: {
      handler () {
        this.$emit('input', {
          start_date: this.form.start_date == '' ? null : this.form.start_date,
          end_date: this.form.end_date == '' ? null : this.form.end_date
        })
      },
      deep: true
    },
    checkBox (newVal) {
      if (newVal) {
        this.form.start_date = newVal.start_date !== null ? newVal.start_date : moment().format('YYYY-MM-DD')
        this.form.end_date = moment().format('YYYY-MM-DD')
      } else {
        this.form.start_date = null
        this.form.end_date = null
      }
    }
  },

  methods: {
    clearDates () {
      this.checkBox = false
      this.form.start_date = null
      this.form.end_date = null
    }
  }
}
